/**
*
* 		LOGIN: Colony Digital
*			- The "Login" page branded for Colony
*
**/
.login {
  background: #000; }
  .login h1 a {
    width: 100%;
    height: 52px;
    background: url("http://colonydigital.ca/images/logos/colony-landing.png") center center no-repeat; }
  .login form,
  .login .message {
    border-radius: 5px; }
  .login #nav {
    float: left;
    padding: 0;
    margin: 10px 0 0; }
  .login #backtoblog {
    float: right;
    padding: 0;
    margin: 10px 0 0; }
