/* EDITOR CSS FILE */
/* ----------------------------------------- */
/**
*
* 		CONFIG
*			- Site Config styles for the site
*
**/
@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,500;1,700&family=Xanh+Mono:ital@0;1&display=swap");
/**
*
* 		MIXINS
*			- Any kind of SCSS Mixins should go in here
*
**/
/**
*
* 		Functions
*			- SASS functions should go in here
*
**/
.bgdark {
  background: #000000; }

.colordark {
  color: #000000; }

.bggray-light {
  background: #f5f5f5; }

.colorgray-light {
  color: #f5f5f5; }

.bggray-dark {
  background: #404042; }

.colorgray-dark {
  color: #404042; }

.bgwhite {
  background: #ffffff; }

.colorwhite {
  color: #ffffff; }

.bgblack {
  background: #000000; }

.colorblack {
  color: #000000; }

/**
*
* 		HELPERS
*			- Any kind of SCSS Helper should go in this file
*
**/
.text-center {
  text-align: center; }

.text-right {
  text-align: right; }

.text-left {
  text-align: left; }

/**
*
* 		DEFAULT
*			- Links the Article page to the Wordpress Editor
*
**/
/**
*
* 		ARTICLE
*			- Note: The Editor styles are pulled from this,
*			- This is using ".article" so you can specify where the editable content would go.
*
**/
body#tinymce {
  background-image: none; }
